.loginContainer {
  animation: fadeIn 1s forwards;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.loginContainer a {
  text-decoration: none;
  color: inherit;
}
.loginGoogle {
  width: 200px;
  height: 50px;
  background-color: white;
  color: black;
  font-weight: 500;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.1s ease-in-out;
  will-change: auto;
}
.loginGoogle img {
  width: 50px;
}
.loginGoogle:hover {
  background-color: rgb(220, 220, 220);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
