.chatContainer {
  width: 100%;
  display: flex;
  height: 80%;
  /* border: 1px solid white; */
  flex-direction: column;
  gap: 15px;
  overflow: scroll;
}
.chatContainer::-webkit-scrollbar {
  display: none;
}
form {
  display: flex;
  flex: 1;
}
input {
  outline: none;
  border: 2px solid black;
  padding: 10px 15px;
  width: 100%;
  transition: ease-in-out 0.3s border;
  border-radius: 5px;
}
input:focus {
  border: 2px solid #4361ee;
}
.inputContainer {
  display: flex;
  width: 100%;
}
.sendMessageButton {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 5px;
  cursor: pointer;
  background-color: black;
  right: 0;
}
