.message {
  background-color: rgb(31, 31, 31);
  border-radius: 10px;
  width: fit-content;
  height: fit-content;
  padding: 15px 20px;
  max-width: 100%;
  word-wrap: wrap;
  word-break: break-word;
  position: relative;
  display: flex;
}
.message img {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin-right: 5px;
}
.message:nth-child(2n) {
  background-color: rgb(27, 27, 27);
}
.messageContent {
  align-self: center;
}

.date {
  font-size: 10px;
  position: absolute;
  top: 100%;
}
